#navbar {
  background-color: #0D3861;
  height: auto;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: auto;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  color: white
}

.hidden-bars {
  display: none
}

.nav-menu {
  background-color: #0D3861;
  width: 250px;
  height: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;;
  color: white;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
  transform: translate(0, 0.1em);
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #0D3861;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

#side-nav-active {
  height: 32em
}

@media screen and (max-width: 1024px) {
  .menu-bars {
    padding-top: 0.2em;
    padding-bottom: 0.2em
  }
}